import { PropTypes } from 'react-desc';

export const navProps = {
  /** props injected to wrapper of page header */
  containerProps: PropTypes.shape({}).description(
    'props injected to wrapper of page header',
  ),
  /** breadcrumbs component */
  breadcrumbs: PropTypes.node.description('breadcrumbs component'),
  /** PageHeaderTitle component */
  pageTitle: PropTypes.node.description('PageHeaderTitle component'),
  /** back arrow component */
  backarrow: PropTypes.node.description('back arrow component'),
  /** PageHeaderSummary component */
  summary: PropTypes.node.description('PageHeaderSummary component'),
  /** toolbar component */
  toolbar: PropTypes.node.description('toolbar component'),
  /** PageHeaderMenu component */
  menu: PropTypes.node.description('PageHeaderMenu component'),
  /** Border bottom below the page header */
  withBottomSeparator: PropTypes.bool.description(
    'Border bottom below the page header',
  ),
};

export const defaultProps = {
  containerProps: {},
  breadcrumbs: null,
  pageTitle: null,
  backarrow: null,
  summary: null,
  toolbar: null,
  menu: null,
  withBottomSeparator: true,
};
