import React from 'react';
import { PropTypes, describe } from 'react-desc';
import { PageHeaderSummaryWrapper } from '../blocks';

const PageHeaderSummary = ({ value = null, label = '', ...rest }) => (
  <PageHeaderSummaryWrapper {...rest}>
    {(value === 0 || value) && <div className="ph-summary-value">{value}</div>}
    {label && <div className="ph-summary-label">{label}</div>}
    {(value === 0 || value || label) && <div className="ph-summary-space" />}
  </PageHeaderSummaryWrapper>
);

const props = {
  /** value for page header summary */
  value: PropTypes.number.description('value for page header summary'),
  /** label attached to value */
  label: PropTypes.string.description('label attached to value'),
};

PageHeaderSummary.propTypes = props;

const DSPageHeaderSummaryWithSchema = describe(PageHeaderSummary);

DSPageHeaderSummaryWithSchema.propTypes = props;

export { PageHeaderSummary, DSPageHeaderSummaryWithSchema };
