import React from 'react';
import { PropTypes, describe } from 'react-desc';
import DSTruncatedTooltipText from '@elliemae/ds-truncated-tooltip-text';
import RequiredMark from '@elliemae/ds-form/RequiredMark';
import { PageHeaderTitleWrapper } from '../blocks';

const PageHeaderTitle = ({ children, showMarker = false, ...rest }) => (
  <PageHeaderTitleWrapper {...rest}>
    <DSTruncatedTooltipText value={children} />
    {showMarker && <RequiredMark required />}
  </PageHeaderTitleWrapper>
);

const props = {
  /** page header title string */
  children: PropTypes.string.description('Page header title string'),
  /** show required mark */
  showMarker: PropTypes.bool.description('show required mark'),
};

PageHeaderTitle.propTypes = props;

const DSPageHeaderTitleWithSchema = describe(PageHeaderTitle);

DSPageHeaderTitleWithSchema.propTypes = props;

export { PageHeaderTitle, DSPageHeaderTitleWithSchema };
